import { Injectable } from '@angular/core';

import {
    AlertServiceAbstractProvider,
    IAlertServiceContract
} from '@polpware/ngx-appkit-contracts-alpha';

import {
    AlertService
} from '@polpware/ngx-appkit-services-alpha';

@Injectable()
export class AlertServiceProvider extends AlertServiceAbstractProvider {

    constructor(private readonly alertService: AlertService) {
        super();
    }

    get(): IAlertServiceContract {
        return this.alertService;
    }
}
