import { Injectable } from '@angular/core';

import {
    ConfigurationServiceAbstractProvider,
    IConfigurationServiceContract
} from '@polpware/ngx-appkit-contracts-alpha';

import {
    ConfigurationService
} from '@polpware/ngx-appkit-services-alpha';

@Injectable()
export class ConfigurationServiceProvider extends ConfigurationServiceAbstractProvider {

    constructor(private readonly impl: ConfigurationService) {
        super();
    }

    get(): IConfigurationServiceContract {
        return this.impl;
    }
}
