import { Injectable } from '@angular/core';

import {
    ThemeManagerAbstractProvider,
    IThemeManagerContract
} from '@polpware/ngx-appkit-contracts-alpha';

import {
    ThemeManager
} from '@polpware/ngx-appkit-services-alpha';

@Injectable()
export class ThemeManagerProvider extends ThemeManagerAbstractProvider {

    constructor(private readonly impl: ThemeManager) {
        super();
    }

    get(): IThemeManagerContract {
        return this.impl;
    }
}
