import { Injectable } from '@angular/core';

import {
    TranslationServiceAbstractProvider,
    ITranslationServiceContract
} from '@polpware/ngx-appkit-contracts-alpha';

import {
    AppTranslationService
} from '@polpware/ngx-appkit-services-alpha';

@Injectable()
export class TranslationServiceProvider extends TranslationServiceAbstractProvider {

    constructor(private readonly impl: AppTranslationService) {
        super();
    }

    get(): ITranslationServiceContract {
        return this.impl;
    }
}
