import { Injectable } from '@angular/core';

import {
    LocalStoreManagerServiceAbstractProvider,
    ILocalStoreManagerContract
} from '@polpware/ngx-appkit-contracts-alpha';

import {
    LocalStoreManager
} from '@polpware/ngx-appkit-services-alpha';

@Injectable()
export class LocalStoreManagerProvider extends LocalStoreManagerServiceAbstractProvider {

    constructor(private readonly impl: LocalStoreManager) {
        super();
    }

    get(): ILocalStoreManagerContract {
        return this.impl;
    }
}
